:root {
  --orange: #f87719;
  --black: #111;
  --second-black: #282828;
  --gray: #595959;
  --light-gray: #b7b7b7;
  --white: #f8f8f8;
  --white-smoke: #fff;
  --liner: linear-gradient(141.22deg, #ffc226 9.4%, #f84119 91.91%);
  --revers-liner: linear-gradient(141.22deg, #f84119 9.4%, rgba(248, 159, 25, .68) 91.91%);
  --transition-dur: 250ms;
  --transition-func: cubic-bezier(.4, 0, .2, 1);
}

h1, h2, h3, h4, h5, h6, p {
  margin-top: 0;
  margin-bottom: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: currentColor;
  text-decoration: none;
}

button {
  cursor: pointer;
  background: none;
  border: none;
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

body {
  letter-spacing: .02em;
  color: var(--white);
  background-color: var(--black);
  overflow: overlay;
  font-family: Roboto, sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.5;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.second-section {
  margin-bottom: 40px;
}

.home-pg-third-section {
  margin-bottom: 32px;
}

@media screen and (min-width: 768) {
  .second-section {
    margin-bottom: 52px;
  }

  .home-pg-third-section {
    margin-bottom: 44px;
  }
}

@media screen and (min-width: 1280) {
  .second-section, .home-pg-third-section {
    margin-bottom: 80px;
  }
}

.main-container {
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
}

@media screen and (min-width: 320px) {
  .main-container {
    width: 280px;
    margin: 0 auto;
  }
}

@media screen and (min-width: 768px) {
  .main-container {
    width: 707px;
    margin: 0 auto;
  }
}

@media screen and (min-width: 1280px) {
  .main-container {
    width: 1216px;
    margin: 0 auto;
  }
}



































